import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
  selector: 'bb-messages-heading',
  templateUrl: './messages-heading.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MessagesHeadingComponent {
  /**
   * Heading title
   */
  @Input() headingTitle = '';

  /**
   * Heading type
   */
  @Input() headingType = 'h1';

  /**
   * Custom heading classes
   */
  @Input() headingClasses = '';

  /**
   * Custom heading wrapper classes
   */
  @Input() headingWrapperClasses = '';

  /**
   * Heading button text
   */
  @Input() buttonText = '';

  /**
   * Flag indicates heading button visibility
   */
  @Input() showButton = true;

  /**
   * Heading heading button press action
   */
  @Output() buttonAction = new EventEmitter<void>();

  onButtonClick() {
    this.buttonAction.emit();
  }
}
